.App {
  text-align: center;
}

.main-container {
  height: calc(100%);
  overflow: auto;
}

.main-container::-webkit-scrollbar {
  display: none;
}

textarea {
  font-family: inherit;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 4px;
  font-size: 14px;
  border: 1px solid var(--light-border-color);
  border-radius: 4px;
}

div,
span,
button,
li {
  user-select: unset;
}

.add-job-container {
  text-align: center;
}

.form-field {
  text-align: left;
}

.k2-form-field {
  margin: 0px 15px;
}

.k2-form-field2 {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  margin-top: 25px;
}


--Added by Yonatan till muiDialogAction -----
.k2-form-field3 {
  flex: 1; /* Let each textbox take up an equal portion of the container */
  padding: 5px;
  box-sizing: border-box;
}

.right-header {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

-- added by yonatan
.input-container {
  display: flex;
  flex-direction: column;
}

.input-field {
  display: flex;
  align-items: center;
  margin-top: 20px;
  width: 100%;
}

.input-field label {
  width: 150px; /* Adjust the width as needed */
  margin-right: 10px;
}

.icon-header-container {
  display: flex;
  align-items: center; /* Vertically align items */
}

.left-header {
  text-align: "left";
}


.iconWrapper-card {
  margin-left: 5px; /* Adjust the margin as needed */
}



h1 {
  margin: 0; /* Remove any default margin from h1 */
}

--yonatan
.input-field input {
  flex: 1;
}

.iconWrapper-card {

  width: 40px;

  height: 40px;

  border-radius: 50%;

  padding: 7px 7px 6px;

  object-fit: contain;

  background-color: #f0832b;

  margin-right: 10px;

}


.MuiDialogActions-root.MuiDialogActions-spacing button:hover {
    color: white;
    background-color: var(--primary-color-hover);
    border-color: var(--primary-color-hover);
  }

  .MuiDialogActions-root.MuiDialogActions-spacing .MuiButton-textPrimary {
    background-color: var(--primary-color);
    color: white;
  }

  hr {
    border: 0;
    height: 0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  }

  * {
    --skeleton-bg: lightgray
  }

  *.dark, *.dark *{
    --primary-color: #1483F3;
    --primary-color-hover: #f0832b;
    --list-hover-bg: #cccccc;
    --secondary-text-color: #f2f2f2;
    --default-text-color: #f2f2f2;
    --header-bg-color: #2e2e2e;
    --header-text-color: #e3e3e3;
    --bg-color: #2e2e2e;
    --light-border-color: #2e2e2e;
    --header-logo-height: 27px;
    --skeleton-bg: #404040
  }

  body.dark .k2-card{
    background: #2A2A2A;
    box-shadow: 0px 0px 0px 1px rgba(255,255,255,0.1);
    border: none;
  }

  body.dark select {
    background-color: white;
  }

  body.dark tr:nth-child(even) {
    background-color: var(--default-text-color);
  }

  body.dark tr:nth-child(odd) {
    background-color: white;
  }

  body.dark tbody tr {
    color: var(--bg-color);
  }

  body.dark .k2-secondary-btn {
    color: var(--bg-color);
  }


  body.dark table {
    --border: 1px solid rgba(255,255,255,0.5);
    background-color: var(--bg-color);
  }

  .k2-breadcrumb {
    display: inline-block;
  }

  body.switching-theme{
    transition: all 0.3s linear;
  }

  .k2-card {
    overflow: auto
  }

  .skeleton {
    background: var(--skeleton-bg) !important;
  }