.empty-table {
    height: auto;
  }
  
  .k2-category-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px 25px;
    width: 100%;
    min-height: calc(100% - 85px);
    overflow: visible;
  }


  .k2-category-container2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px 25px;
    width: 100%;
    min-height: calc(100% - 85px);
    overflow: visible;
  }

  .k2-category-container3 {
    display: flex;
    flex-direction: column;
    padding: 0px 25px;
    width: 100%;
    min-height: calc(100% - 85px);
    overflow: visible;
  }

  .k2-category-container3 label.formLabel {
    width: 150;
    margin-right: 10px;
  }


  .k2-category-container .k2-card {
    width: 100%;
  }

  .k2-category-container .k2-card.k2-category-table {
    margin-top: 10px;
    flex: 1
  }
  .reactTableCell {
    overflow: hidden;
    text-overflow: ellipsis;
    user-select: all;
  }
  
  .empty-table {
    height: 150px;
  }
  
  .k2-category-container .k2-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  --yonatan
  .header-content {
    display: flex; /* Use flexbox to keep elements on the same line */
    align-items: center; /* Vertically align content in the header */
  }
  
  
  .k2-body {
    overflow: hidden;
    display: block;
    height: auto;
    transition-property: height;
    transition-duration: 0.3s;
    transition-timing-function: ease-in;
  }
  
  /* @media screen and (max-width: 1071px) {
    .k2-category-container .k2-body {
      height: 277px;
    }
  }
  
  @media screen and (min-width: 1916px) {
    .k2-category-container .k2-body {
      height: 138px;
    }
  } */
  
  .k2-body.closed {
    height: 0;
    transition-property: height;
    transition-duration: 0.3s;
    transition-timing-function: ease-in;
    transition-delay: 0s;
    overflow: hidden;
  }
  
  .k2-category-container .k2-header .k2-header-actions {
    display: flex;
  }
  
  .k2-category-container .k2-header .k2-header-field {
    margin: 0px 10px;
  }
  
  .k2-category-container .k2-header .k2-header-field:last-child {
    margin-right: 0px;
  }
  
  .k2-category-container .k2-header .k2-header-field label {
    padding-right: 10px;
  }
  
  .k2-auto-refresh {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    height: 100%;
  }
  .k2-add-container {
    display: flex;
    align-items: center;
    position: relative;
  }
  
  .k2-add-container form {
    display: flex;
    align-items: flex-start;
    flex-grow: 1;
    flex-wrap: wrap;
  }
  
  .k2-add-title {
    padding-top: 15px;
    font-size: 16px;
  }
  
  .form2 {
    display: flex;
    align-items: center;
    flex-grow: 1;
    flex-wrap: wrap;
    flex-direction: column;
    height: 180px;
    justify-content: flex-end;
  }
  
  .k2-add-container .k2-txt-btn.k2-close {
    position: absolute;
    top: -7px;
    right: -15px;
  }
  
  .k2-button-loader {
    margin: 10px auto !important;
  }
  
  .help-icon {
    color: #fff;
    background-color: var(--primary-color);
    width: 12px;
    height: 12px;
    display: inline-block;
    border-radius: 100%;
    font-size: 12px;
    text-align: center;
    text-decoration: none;
    -webkit-box-shadow: inset -1px -1px 1px 0px rgba(0, 0, 0, 0.25);
    -moz-box-shadow: inset -1px -1px 1px 0px rgba(0, 0, 0, 0.25);
    box-shadow: inset -1px -1px 1px 0px rgba(0, 0, 0, 0.25);
    cursor: pointer;
    align-self: center;
    font-weight: normal;
    position: relative;
    top: -7px;
  }
  .command-tooltip {
    position: fixed;
    z-index: 2;
    min-width: 400px;
  }
  .blink {
    animation: blink-animation 1s steps(5, start) infinite;
    -webkit-animation: blink-animation 1s steps(5, start) infinite;
  }
  @keyframes blink-animation {
    to {
      visibility: hidden;
    }
  }
  @-webkit-keyframes blink-animation {
    to {
      visibility: hidden;
    }
  }
  a {
    cursor: pointer;
  }
  .breadcrumb-custom {
    padding: 0px 25px;
    display: inline-block;
  }
  
  .header-collapse::before {
    content: '>';
    font-family: monospace;
    display: block;
    transition: transform .3s ease;
    transform: rotate(0);
  }
  
  .collapsed.header-collapse::before {
    transform: rotate(180deg);
  }